import React, { Component } from 'react'
import { Link, withRouter } from "react-router-dom";
import "./index.less";

const breadcrumbNameMap = {
    '/gojs': 'Go JS 入门教程',
    '/gojs/0': '介绍',
    '/gojs/1': '第一节 简单的GoJS demo',
    '/gojs/2': '第二节 GoJS应用中有什么',
    '/gojs/3': '第三节 使用GoJS构建Parts(Parts 和 Panels)',
    '/gojs/4': '第四节 GoJS Diatram Model',
    '/gojs/5': '第五节 使用GoJS构建Parts(Templates)',
};

class index extends Component {
    render() {
        const pathSnippets = this.props.location.pathname;
        let length = pathSnippets.length, lastStr = Number(pathSnippets[length - 1]), href = pathSnippets.slice(0, length - 1);
        let prevHref = href + (lastStr - 1);
        let nextHref = href + (lastStr + 1);
        const prev = breadcrumbNameMap[prevHref] ?
            <Link to={prevHref} className="pager">
                <div className="label">上一篇</div>
                <span className="title">{breadcrumbNameMap[prevHref]}</span>
            </Link>
            : null
        const next = breadcrumbNameMap[nextHref] ?
            <Link to={nextHref} className="pager">
                <div className="label">下一篇</div>
                <span className="title">{breadcrumbNameMap[nextHref]}</span>
            </Link>
            : null
        return (
            <div className="page-footer">
                <div className="content">
                    <div className="prev">{prev}</div>
                    <div className="next">{next}</div>
                    <div style={{height:"100vh"}}></div>
                </div>
            </div>
        )
    }
}

export default withRouter(index);